.products {
  &__title,
  &__subtitle {
    font-family: 'Titan One';
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
  }

  &__title {
    color: $accent-color;
  }

  &__title-part {
    display: block;
    color: $secondary-color;
  }

  &__item {
    position: relative;
    border-radius: 24px;

    &--pink {
      background-color: $products-pink-color;
    }
    &--sand {
      background-color: $products-sand-color;
    }
    &--green {
      background-color: $products-green-color;
    }

    &--pink::before,
    &--sand::before,
    &--green::before {
      position: absolute;
      display: block;
      top: -27%;
      right: 0;
    }

    &--pink::before {
      content: url(../images/products/ice-cream1@1x.webp);
    }

    &--sand::before {
      content: url(../images/products/ice-cofee2@1x.webp);
    }

    &--green::before {
      content: url(../images/products/milkshake3@1x.webp);
    }
  }

  &__subtitle,
  &__text {
    color: $main-white-color;
    text-align: center;
  }

  &__subtitle {
    font-size: 20px;
  }

  &__dots {
    text-align: center;
    margin: 15px 0;
  }

  &__dot {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: $main-white-color;
    display: inline-block;
  }

  &__text {
    font-size: 14px;
    font-weight: 700;
  }

  &__btn {
    margin: 0 auto;
  }
  

  @media screen and (max-width: 768px) {

    padding: 80px 0 40px;

    .products {

      &__title {
        font-size: 34px;
        margin-bottom: 150px;
      }

      &__item {
        padding: 160px 40px 40px;
        max-width: 280px;
        margin: 0 auto;

        &:not(:last-child) {
          margin-bottom: 150px;
        }
      }

      &__text {
        margin-bottom: 32px;

      }
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1200px) {

    padding: 40px 0 60px;
    .products {

      &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 900px;
        margin: 0 auto;
      }

      &__item {
        padding: 120px 32px 40px;
        
        &:not(:last-child) {
          margin-right: 16px;
        }

        &--pink::before,
        &--sand::before,
        &--green::before {
          top: -40%;
          right: 0;
        }
      }

      &__title {
        font-size: 48px;
        margin-bottom: 190px;
      }

      &__title-part {
        font-size: 26px;
      }

      &__text {
        margin-bottom: 42px;
      }
    }
  }

  @media screen and (min-width: 1200px) {
    .products { 
      
      &__title {
        font-size: 58px;
        margin-bottom: 230px;
      }

      &__title-part {
        font-size: 26px;
      }


      &__list {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      &__subtitle {
        font-size: 30px;
      }

      &__text {
        font-size: 16px;
        margin-bottom: 60px;
      }

      &__item {

        padding: 165px 75px 60px;
 
        padding: 265px 32px 40px;
        
        &:not(:last-child) {
          margin-right: 32px;
        }


        &--pink::before,
        &--sand::before,
        &--green::before {
          top: -47%;
        }

        &--pink::before {
          content: url(../images/products/ice-cream1@desktop.webp);
        }

        &--sand::before {
          content: url(../images/products/ice-cofee2@desktop.webp);
        }

        &--green::before {
          content: url(../images/products/milkshake3@desktop.webp);
        }
      }
    }
  }
}
